import React from 'react';
import Roads from './images/Roads.jpg';
import { NavLink } from 'react-router-dom';
import Common from './Common';

function About(){
    return(
    <>
        <Common 
            name='Welcome to About Us '
            btname='Contact now'
            visit='/contact'
            img={Roads}
            para='This is will be the loyal to give your appreciation'
        />   
    </>);

}
export default About;